export class CBUtils {
    static getNodeTree = (item: any, nodes: any[]) => {
        let current: any = item;
        let treePath: any[] = [];
        let levelLimit = 2;
    
        if (Array.isArray(current['tree_path'])) {
            treePath = [...current['tree_path']].reverse();
        } else {
            treePath = Object.values(current['tree_path']).reverse();
        }
        treePath.shift();
    
        if (current.level === 3) {
            levelLimit = 1;
        }
    
        const nodeTree = treePath.reduce((result, path) => {
            if (current !== undefined && current.level > levelLimit) {
    
                const input = {
                    id: current.id,
                    name: current.name,
                    code: current.code
                };
    
                if (current['original_id']) {
                    input['original_id'] = current['original_id'];
                }
    
                result.push(input);
    
                current = nodes.find((node: any) => {
                    return node.level === (current.level - 1) &&
                        node.code === path &&
                        node.children.includes(current.id);
                });
            }
            return result;
        }, new Array()).reverse();
    
        return nodeTree;
    } 

    static convertNodeData = (nodesParam: any[], loadExisting = false) => {
        let nodeIDMap: any[] = [];
    
        let nodes = [...nodesParam];
    
        if (!loadExisting) {
            nodes = nodes.filter ((item: any) => {
                if (item.level === 1 || 
                    item.level === 2 || 
                    (item.level > 2 && item.tree_path[2] === 'BASE')
                ) {
                    return true;
                } 
                return false;
            });
        }
    
        nodes = nodes.sort ((node1: any, node2: any) => {
            if (node1.level === 3 && node1.code === 'BASE') return -1;
            if (node2.level === 3 && node2.code === 'BASE') return 1;
    
            if (node1.sort_index > node2.sort_index) return -1;
            if (node1.sort_index < node2.sort_index) return 1;
    
            if (node1.name > node2.name) return 1;
            if (node1.name < node2.name) return -1;
    
            return 0;
        });
    
        nodes = nodes.map ((item: any, i: number) => {
            const oldID = item['_id'];
            let itemPlanType = 'BASE';
    
            if (item.level > 2 && item.tree_path[2] !== 'BASE') {
                itemPlanType = item.tree_path[2];
            }
    
            const newID = "NODE-" + itemPlanType + "-"+i.toString().padStart(3, '0');
    
            item['included'] = true;
    
            const IDSet = {
                id: oldID,
                newID: newID
            };
    
            let metadata = Object.assign({}, item['custom_metadata']);
    
            if (metadata['included_facilities'] && metadata['included_facilities'].length > 0) {
                const newIncludedFacilities = [...metadata['included_facilities']];
    
                metadata['partner_network_list'] = newIncludedFacilities.map((item: any, i: number) => {
                    let newPartner = Object.assign({}, item);
    
                    newPartner['parent'] = newID;
                    newPartner['included'] = true;
                    newPartner['index'] = i;
    
                    return newPartner;
                });
    
                delete metadata.included_facilities;
            }
    
            if (metadata['excluded_facilties'] && metadata['excluded_facilties'].length > 0) {
                let newExcludedFacilities = [...metadata['excluded_facilties']];
    
                let partnerNetworkList: any[] = [];
    
                if (metadata['partner_network_list'] !== undefined) {
                    partnerNetworkList = [...metadata['partner_network_list']];
                }
    
                newExcludedFacilities = newExcludedFacilities.map((item: any, i: number) => {
                    let newPartner = Object.assign({}, item);
    
                    newPartner['parent'] = newID;
                    newPartner['included'] = false;
                    newPartner['index'] = i;
    
                    return newPartner;
                });
    
                metadata['partner_network_list'] = [...partnerNetworkList, ...newExcludedFacilities];
    
                delete metadata.excluded_facilties;
            }
    
            item['custom_metadata'] = metadata;
    
            nodeIDMap.push (IDSet);
    
            if (loadExisting) {
                item['original_id'] = item['_id'];
            }
    
            item['id'] = newID;
    
            delete item['_id'];
    
            return Object.assign({}, item);
        });
    
        nodes = nodes.map ((item: any) => {
            if (loadExisting) {
                item['original_children'] = [...item['children']];
            }
    
            if (item['children'].length > 0) {
                item['children'] = item['children'].reduce ((result, id) => {
                    const found = nodeIDMap.find ((set) => { return set.id === id; });
    
                    if (found) {
                        result.push(found.newID)
                    }
    
                    return result;
                }, []);
            }
            return item;
        });
    
        return nodes;
    }
    
    static createExclusionList = (nodes: any[]) => {
        const eList = nodes.reduce((exResult, item: any) => {
            // if (item['custom_metadata']['is_excluded'] && item['custom_metadata']['is_excluded'] === true) {
            if (item['level'] === 6 && item['tree_path'][3] === 'EX') {
                const exTree = CBUtils.getNodeTree(item, nodes);
                exResult.push(exTree);
            }
            return exResult;
        }, []);
    
        return eList;
    }

    static combineNodes = (parentNodes, childNodes) => {
        const nodes = parentNodes.reduce ((result, parent) => {
          parent.children.map ((child) => {
            let foundChild = childNodes.find ((childNode) => child === childNode.id);
      
            if (foundChild) {
              let child = Object.assign({}, foundChild);
      
              child['parent_name'] = parent.name;
              child['parent_code'] = parent.code;
              child['disabled'] = false;
              result.push(child);
            }
          });
      
          return result;
        }, []);
      
        return nodes;
    }
      
    static combineNodes2 = (parentNodes, childNodes) => {
        const nodes = parentNodes.reduce ((result, parent) => {
      
          let newParent = Object.assign({}, parent);
          let newChildNodes: any[] = []; 
      
          newParent.children.map ((child) => {
            let foundChild = childNodes.find ((childNode) => child === childNode.id);
      
            if (foundChild) {
              let child = Object.assign({}, foundChild);
      
              child['disabled'] = false;
              newChildNodes.push(child);
            }
          });
      
          newParent['child_nodes'] = newChildNodes;
          newParent['disabled'] = false;
      
          result.push(newParent);
      
          return result;
        }, []);
      
        return nodes;
    }
      
    static getFilteredNodes = (nodes, level) => {
        return nodes.filter((item: any) => {
          return (
            item.level === level
          );
        });
    }
      
    static getSidebarList = (benefitCategoryNodes, planType, planPackage) => {
        return benefitCategoryNodes.reduce((result, item: any) => {
      
          if (item.tree_path[2] === planType && item.tree_path[3] === planPackage) {
            let newItem = Object.assign({}, item);
      
            if (planType !== 'BASE') {
              const baseNode = benefitCategoryNodes.find ((baseItem) => {
                return (
                  baseItem.name === newItem.name &&
                  baseItem.code === newItem.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === newItem.tree_path[3] &&
                  baseItem.tree_path[4] === newItem.tree_path[4] 
                );
              });
      
              if (baseNode && baseNode.included === false) {
                newItem['base_disabled'] = true;
              } else {
                newItem['base_disabled'] = false;
              }
            };
      
            result.push(newItem);
          }
            
          return result;
        }, new Array());
    }
      
    static getRows = (exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
        let rows: any[] = [];
        if (['EX'].includes(selectedBenefitCategory)) {
          if (exclusionList.length > 0) {
            rows = exclusionList.reduce ((result, exclusionArray) => {
              const code = exclusionArray[0].code;
              
              if (code === selectedPlanType) {
                let exclusion = Object.assign({}, exclusionArray[exclusionArray.length - 1]);
      
                const parent = Object.assign({}, exclusionArray[exclusionArray.length - 2]);
      
                exclusion['parent_name'] = parent.name;
                exclusion['parent_code'] = parent.code;
                exclusion['disabled'] = false;
                exclusion['included'] = false;
      
                const found = benefitTypeNodes.find((item: any) => {
                  return item.id === exclusion.id;
                });
      
                if (found && found['custom_metadata'] && found['custom_metadata']['is_excluded'] !== undefined) {
                  const metadata = Object.assign({}, found['custom_metadata']);
                  exclusion['included'] = metadata['is_excluded'];
                }
      
                result.push(exclusion);
              }
      
              return result;
            }, []);
          }
        } else if (['ID','NT'].includes(selectedBenefitCategory)) {
          const categoryNodes = benefitCategoryNodes.filter ((item: any) => {
            return item.tree_path[2] === selectedPlanType &&
            item.tree_path[3] === selectedBenefitCategory;
          });
          const typeNodes = benefitTypeNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedBenefitCategory
            );
          });
      
          rows = CBUtils.combineNodes2 (categoryNodes, typeNodes);
      
        } else if (['EL'].includes(selectedBenefitCategory)) {
          const benefitNodes = mainBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory &&
              item.tree_path[5] === 'DP'
            );
          });
      
          const subNodes = subBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory &&
              item.tree_path[5] === 'DP'
            );
          });
      
          rows = CBUtils.combineNodes2 (benefitNodes, subNodes);
      
          if (selectedPlanType !== 'BASE') {
              rows = rows.map ((item) => {
                item.child_nodes.map((child) => {
                  const baseNode = subBenefitNodes.find ((baseItem) => {
                    return (
                      baseItem.name === child.name &&
                      baseItem.code === child.code &&
                      baseItem.tree_path[2] === 'BASE' &&
                      baseItem.tree_path[3] === child.tree_path[3] &&
                      baseItem.tree_path[4] === child.tree_path[4] &&
                      baseItem.tree_path[5] === child.tree_path[5] &&
                      baseItem.tree_path[6] === child.tree_path[6] &&
                      baseItem.tree_path[7] === child.tree_path[7]
                    )
                  });
      
                  if (baseNode && baseNode.included === true) {
                    child['disabled'] = true;
                  } else {
                    child['disabled'] = false;
                  }
      
                  return child;
                })
                return item;
            });
          }
      
        } else if (['RBC'].includes(selectedBenefitCategory))  {
          rows = [...benefitCategoryNodes];
      
          rows = rows.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage 
            );
          });
      
          rows = rows.map ((item) => {
            item['disabled'] = false;
            return item;
          });
        } else if (['EM','DNT','CC','EC','PV','SH','ADD','SD'].includes(selectedBenefitCategory)) {
          rows = [...benefitTypeNodes];
      
          rows = rows.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory
            );
          });
      
          if (selectedPlanType !== 'BASE' && selectedBenefitCategory !== 'SH') {
            rows = rows.map ((item) => {
              const baseNode = benefitTypeNodes.find ((baseItem) => {
                return (
                  baseItem.name === item.name &&
                  baseItem.code === item.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === item.tree_path[3] &&
                  baseItem.tree_path[4] === item.tree_path[4] &&
                  baseItem.tree_path[5] === item.tree_path[5] 
                )
              });
      
              if (baseNode && baseNode.included === true) {
                item['disabled'] = true;
              } else {
                item['disabled'] = false;
              }
      
              return item;
            });
          } else {
            rows = rows.map ((item) => {
              item['disabled'] = false;
              return item;
            });
          }
          return rows;
        } else if (['MT','APE', 'IP', 'OP', 'OM','PPE'].includes(selectedBenefitCategory)) {
          const typeNodes = benefitTypeNodes.filter ((item: any) => {
            return item.tree_path[2] === selectedPlanType &&
            item.tree_path[4] === selectedBenefitCategory;
          });
          const benefitNodes = mainBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory
            );
          });
      
          rows = CBUtils.combineNodes (typeNodes, benefitNodes);
      
          if (selectedPlanType !== 'BASE') {
            rows = rows.map ((item) => {
              const baseNode = mainBenefitNodes.find ((baseItem) => {
                return (
                  baseItem.name === item.name &&
                  baseItem.code === item.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === item.tree_path[3] &&
                  baseItem.tree_path[4] === item.tree_path[4] &&
                  baseItem.tree_path[5] === item.tree_path[5] &&
                  baseItem.tree_path[6] === item.tree_path[6]
                )
              });
      
              if (baseNode && baseNode.included === true) {
                item['disabled'] = true;
              } else {
                item['disabled'] = false;
              }
      
              return item;
            });
          }
      
        } else {
          rows = mainBenefitNodes.filter((item: any) => {
              return (
                item.tree_path[2] === selectedPlanType &&
                item.tree_path[3] === selectedPlanPackage &&
                item.tree_path[4] === selectedBenefitCategory
              );
            }
          );
        }
      
        if (selectedBenefitCategory !== 'EX') {
          rows = rows.filter((item: any) => {
            if (item['custom_metadata'] && item['custom_metadata']['standard_exclusion'] !== undefined) {
              return item['custom_metadata']['standard_exclusion'] ? false : true;
            }
            return true;
          });
      
        } 
      
        return rows;
    }
      
    static getContractNode = (contractNodes) => {
        return contractNodes.find(item => 
          item.code === 'CT'
        );
    }
      
    static getCategoryNode = (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
        if (['RBC', 'EX'].includes(selectedBenefitCategory)) {
          return planPackageNodes.find(item => 
            item.code === selectedPlanPackage &&
            item.tree_path[2] === selectedPlanType && 
            item.tree_path[3] === selectedPlanPackage 
          );
        } else if (selectedBenefitCategory === 'NT') {
            return planPackageNodes.find(item => 
              item.code === selectedBenefitCategory &&
                item.tree_path[2] === selectedPlanType && 
                item.tree_path[3] === selectedBenefitCategory 
              );
        } else {
          return benefitCategoryNodes.find(item => 
            item.code === selectedBenefitCategory &&
            item.tree_path[2] === selectedPlanType && 
            item.tree_path[3] === selectedPlanPackage && 
            item.tree_path[4] === selectedBenefitCategory
          );
        }
    }    
    
    static getPlanTypeList = (planTypeNodes, mainBenefitNodes, benefitTypeNodes) => {
        return planTypeNodes.map((item: any) => {
          item['count'] = mainBenefitNodes.reduce ((result, benefit) => {
            if (benefit.tree_path[2] === item.code && benefit.included) {
              result++;
            }
            return result;
          }, 0);
      
          item['count'] = benefitTypeNodes.reduce ((result, benefit) => {
            if (  benefit.tree_path[2] === item.code && 
                  ['CV'].includes(benefit.tree_path[3]) &&  
                  ['EM'].includes(benefit.tree_path[4]) &&
                  benefit.included 
              ) {
                result++;
            }
            return result;
          }, item['count']);
      
      
          return item;
        });
    }
}